<!-- 提交成功 -->
<template>
	<view>
		<cu-custom bgColor="bg-gradual-blue" :isBack="true">
			<block slot="content"></block>
		</cu-custom>
		<view class="mainsub">
			<view class="padding-xs flex align-center">
				<view class="flex-sub text-center flexmy">
					<!-- <view class="text-xsl padding">
						<text class=" cuIcon-roundcheckfill text-green"></text>
					</view> -->
					<view style="margin: 40upx;">
						<ayQrcode ref="qrcode" :modal="modal_qr" :url="url" @hideQrcode="hideQrcode" :height="200"
							:width="200" />
					</view>
					<view class="textclass" style="margin-top: 50px;">预约用户：{{form.people}}</view>
					<view class="textclass">预约时间：{{form.datastr}}</view>
					<view class="textclass">预约座位：{{floorseat}}</view>
				</view>
			</view>
		</view>
	</view>
</template>

<script>
	import ayQrcode from "@/components/ay-qrcode/ay-qrcode.vue"
	export default {
		onLoad(options) {
			let that = this;
			that.showQrcode(); //一加载生成二维码
			this.form.people = options.people
			this.form.seatid = options.seatid
			this.form.datastr = options.datastr
			this.form.account = options.account
			this.init()
		},
		mounted() {

		},
		components: {
			ayQrcode
		},
		data() {
			return {
				showsend: true,
				count: '',
				timer: null,
				form: {
					people: '',
					seatid: '',
					datastr: '',
					account: ''
				},
				floorseat: '',
				modal_qr: false,
				url: 'https://pixabay.com/images/search/?order=ec', // 要生成的二维码值
			}
		},
		methods: {
			async init() {
				let obj = {
					id: this.form.seatid
				}
				let resdata = await new this.Request(this.Urls.m().seatquerybyid, obj).modepost()
				console.log(resdata)
				this.floorseat = resdata[0].seatdesc
			},
			showQrcode() {
				let _this = this;
				this.modal_qr = true;
				// uni.showLoading()
				setTimeout(function() {
					// uni.hideLoading()
					_this.$refs.qrcode.crtQrCode()
				}, 50)
			},

			//传入组件的方法
			hideQrcode() {
				this.modal_qr = false;
			},

		},
		watch: {
			count(val) {
				let that = this
				if (val == 0) {
					//跳转至主页面
					uni.navigateTo({
						url: `../index/index`
					});
				}
			},
		}
	}
</script>

<style scoped>
	.flexmy{
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}
	
	
	.text-xsl {
		font-size: 150px;
	}

	.mainsub {
		margin-top: 20%;
	}

	.textclass {
		margin-top: 5px;
		font-weight: 600;
		font-size: 1.2em;
	}

	.thanks_titlesmall {
		margin-top: 2%;
		font-size: 1em;
	}

	.thanks_titlesmallsub {
		color: #39b54a;
		font-weight: bold;
	}
</style>
